/* App.css - Mobile-First Design (Optimized for Mobile, then Desktop) */

@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&family=Cinzel&display=swap');

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Playfair Display', serif;
  background-color: #000000;
  color: #ffffff;
  min-height: 100vh;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: linear-gradient(to bottom, #0f0f0f, #000000);
}

.header {
  text-align: center;
  padding: 30px 20px 20px;
  animation: fadeIn 1.5s ease-in-out;
}

.title {
  font-family: 'Cinzel', serif;
  font-size: 2.4rem;
  color: #d4af37;
  margin: 0;
  text-shadow: 0 0 12px rgba(212, 175, 55, 0.4);
}

.subtitle {
  font-size: 1.1rem;
  color: #b3b3b3;
  margin-top: 8px;
}

.main-content {
  flex: 1;
  padding: 20px;
  width: 100%;
  max-width: 100%;
}

.feedback-form {
  display: flex;
  flex-direction: column;
  gap: 32px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 16px;
  padding: 24px;
  box-shadow: 0 0 25px rgba(212, 175, 55, 0.15);
  animation: slideUp 1s ease-out;
}

.form-title {
  font-size: 1.8rem;
  color: #d4af37;
  text-align: center;
  margin: 0 0 10px 0;
}

.form-description {
  text-align: center;
  color: #cccccc;
  font-size: 1rem;
  line-height: 1.5;
  margin-bottom: 10px;
}

.section {
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.section h3 {
  color: #d4af37;
  font-size: 1.25rem;
  margin: 0;
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(212, 175, 55, 0.3);
}

input[type="text"],
input[type="tel"],
input[type="date"],
textarea {
  padding: 16px;
  border: 1px solid #d4af37;
  border-radius: 10px;
  background: #1a1a1a;
  color: #ffffff;
  font-size: 1.05rem;
  width: 100%;
}

input::placeholder,
textarea::placeholder {
  color: #888;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: #ffd700;
  box-shadow: 0 0 15px rgba(255, 215, 0, 0.3);
}

textarea {
  resize: vertical;
  min-height: 100px;
}

.date-group {
  display: flex;
  flex-direction: column;
}

.date-label {
  font-size: 0.9rem;
  color: #b3b3b3;
  margin-top: 6px;
  margin-left: 4px;
}

.rating-group,
.radio-group,
.checkbox-group {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.rating-label,
.radio-group label,
.checkbox-group label {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: 1.1rem;
  cursor: pointer;
}

.rating-label input,
.radio-group input,
.checkbox-group input {
  accent-color: #d4af37;
  width: 20px;
  height: 20px;
}

.rating-label span {
  flex: 1;
}

.submit-button {
  margin-top: 20px;
  padding: 16px;
  background-color: #d4af37;
  color: #000000;
  border: none;
  border-radius: 10px;
  font-size: 1.2rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  align-self: center;
  min-width: 200px;
}

.submit-button:hover {
  background-color: #ffd700;
  transform: translateY(-4px);
  box-shadow: 0 10px 25px rgba(255, 215, 0, 0.3);
}

.thank-you {
  text-align: center;
  padding: 60px 20px;
  animation: fadeIn 1.5s ease-in-out;
}

.thank-you h2 {
  color: #d4af37;
  font-size: 2.2rem;
  margin-bottom: 20px;
}

.thank-you p {
  font-size: 1.2rem;
  color: #cccccc;
  line-height: 1.6;
}

.footer {
  text-align: center;
  padding: 20px;
  color: #808080;
  font-size: 0.9rem;
}

/* Animations */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(30px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

/* Desktop View - Enhancements (min-width: 768px) */
@media (min-width: 768px) {
  .header {
    padding: 50px 20px 30px;
  }

  .title {
    font-size: 3.5rem;
  }

  .subtitle {
    font-size: 1.4rem;
  }

  .main-content {
    padding: 40px;
    max-width: 900px;
    margin: 0 auto;
  }

  .feedback-form {
    padding: 40px;
    gap: 40px;
  }

  .form-title {
    font-size: 2.4rem;
  }

  .form-description {
    font-size: 1.2rem;
  }

  .section h3 {
    font-size: 1.4rem;
  }

  .rating-group,
  .radio-group {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .rating-label,
  .radio-group label {
    width: 48%;
  }

  .checkbox-group {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
  }

  .submit-button {
    padding: 18px 50px;
    font-size: 1.3rem;
  }

  .thank-you h2 {
    font-size: 3rem;
  }
}

/* Larger Desktop */
@media (min-width: 1024px) {
  .checkbox-group {
    grid-template-columns: repeat(3, 1fr);
  }

  .rating-label,
  .radio-group label {
    width: auto;
    min-width: 200px;
  }
}